.card-overview {
  .br-1 {
    border-right: 1px solid var(--border-color);

    @include lg {
      border: 0 none;
    }
  }

  @include e("left") {
    .col-xl-6 {
      padding-right: 12.5px;

      @include sm {
        padding-right: 12.5px;
      }

      &.br-1 {
        padding-right: 25px;
        padding-left: 12.5px;

        @include lg {
          padding-right: 12.5px;
        }
      }
    }
  }

  @include e("right") {
    margin-right: 3px;

    .col-xl-6 {
      padding-left: 25px;

      @include lg {
        padding-left: 12.5px;
      }

      @include sm {
        padding-right: 12.5px;
      }

