/* overview single chart */

.overview-content {
  background-color: var(--color-white);
}

.overview-single {
  padding: 30px 0 25px;

  .overview-content {
    p {
      font-size: 14px;
      margin-top: 2px;
      color: var(--color-lighten);
    }

    small {
      font-size: 13px;
      margin-left: 6px;
      color: var(--color-lighten);
    }

    img,
    svg {
      width: 15px;
      margin-right: 6px;
    }

    strong {
      font-weight: 500;
    }
  }
}

.ap-po-timeChart {
  .parentContainer {
    position: relative;
    bottom: -6px;
  }
}

/* Card Bar chart */
.card-chart-bar {
  align-items: flex-end;
  margin-bottom: 25px;
}

.legend-static {
  li {
    font-size: 13px;

    &:not(:last-child) {
      margin-right: 15px;
    }

    span {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      margin-right: 7px;
    }

    &.custom-label {
      display: inline-flex;
      align-items: center;
    }
  }
}

/* bar chart top */
.card-bar-top {
  p {
    margin-bottom: 6px;
    color: var(--color-lighten);
  }

  .card-bar-info {
    sub {
      font-size: 14px;
      font-weight: 600;
      margin: 0 0 0 6px;

      svg,
      img {
        width: 14px;
      }
    }
  }
}

/* performance chart */
.perfomence-chart {
  .legend-static {
    margin-top: 20px;
  }
}

.performance-stats {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;

  div {
    padding: 20px;
    transition: var(--transition);
    position: relative;

    @include xl {
      padding: 10px;
    }

    @include xxs {
      flex: 0 0 100%;
      max-width: 100%;
    }

    span {
      color: var(--color-lighten);
      font-size: 14px;
      margin-bottom: 13px;
    }

    strong {
      @include rfs(24px);
      font-weight: 600;
      color: var(--color-dark);

      @include xl {
        @include rfs(20px);
      }

      sub {
        bottom: 0;
        font-size: 14px;
        font-weight: 500;
      }
    }

    &:hover {
      box-shadow: 0 15px 30px rgba(var(--light-gray-rgba), 0.15);
      z-index: 1;

      span {
        color: var(--color-primary);
      }
    }
  }

  span,
  strong {
    display: block;
  }

  a {
    &.active {

      .performance-stats__up,
      .performance-stats__down {
        background: var(--bg-lighter);

        &:hover {
          box-shadow: 0 15px 30px rgba(var(--light-gray-rgba), 0) !important;
        }
      }
    }
  }

  @include e("up") {
    background: var(--color-white);

    strong {
      sub {
        color: var(--color-success);
      }
    }
  }

  @include e("down") {
    strong {
      sub {
        color: var(--color-danger);
      }
    }
  }
}

// Session Pie Chart
.demo3 {
