/* chart doughnut legend */
.session-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 18px 0;

  &.session-wrap--2 {
    justify-content: space-between;
    padding: 0 15px;
    min-height: 76px;
    background: #f8f9f8;
    border-radius: 8px;
    margin-bottom: 0;

    .session-single {
      strong {
        font-weight: 600;
      }

      .chart-label {
        font-size: 15px;
        font-weight: 400;
        line-height: lh(15px, 25px);
        color: var(--color-gray);
        margin-bottom: 0;
      }
    }
  }

  &.session-wrap--top {
    margin: 18px 0 0 0;

    .session-single .chart-label {
      margin-bottom: 2px;
      font-size: 15px;
      font-weight: 500;
      line-height: lh(15px, 20px);
      color: var(--color-dark);
    }

    &--2 {
      margin: 25px 0 10px 0;
    }

    &--4 {
      margin: 8px 0 -4px 0;
    }

    &--5 {
      margin: 18px 0 0 0;
    }

  }

  @include md {
    margin: 10px 0 -5px;
  }

  .session-single {
    text-align: center;
    padding: 5px 15px;

    @include xl {
      padding: 15px;
    }

    @include md {
      padding: 10px;
    }

    @include ssm {
      padding: 5px;
    }

    .chart-label {
      display: flex;
      align-items: center;
      color: var(--color-dark);
      font-size: 15px;
      font-weight: 500;
      margin-bottom: 5px;

      &.new {
        color: var(--color-gray);
        font-size: 14px;
        font-weight: 400;
      }
    }

    strong {
      font-weight: 500;
      @include rfs(18px);
      color: var(--color-dark);
    }

    sub {
      bottom: 0;
      font-size: 13px;
      color: var(--color-lighten);
    }
  }
}

.label-dot {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 9px;

  &.dot-primary {
    background-color: var(--color-primary);
  }

  &.dot-secondary {
    background-color: var(--color-secondary);
  }

  &.dot-success {
    background-color: var(--color-success);
  }

  &.dot-info {
    background-color: var(--color-info);
  }

  &.dot-warning {
    background-color: var(--color-warning);
  }
}

/* Revenue Chart */
.revenue-labels {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -25px 20px;

  div {
    padding: 25px;

    @include sm {
      padding-bottom: 0;
    }
  }

  strong {
    @include rfs(24px);
    display: block;
    font-weight: 600;
    color: var(--color-dark);
  }

  span {
    font-size: 14px;
    color: var(--color-lighten);
    display: block;
    position: relative;
    padding-left: 17px;

    &:before {
      position: absolute;
      content: "";
      width: 7px;
      height: 7px;
      border-radius: 50%;
