    }

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    .inEx-subtitle {
      display: inline-block;
      font-size: 14px;
      margin-bottom: 8px;
      color: var(--color-light);
    }

    .inEx__info {
      @include xxl {
        flex-flow: column;
