/* Chart label detail */
.label-detailed {
  display: flex;
  justify-content: center;
  margin: -16px -12px -5px;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 11px;

  @include sm {
    margin-bottom: 0;
    gap: 0;
  }

  &--two {
    margin: 0 -12px -5px;

    .label-detailed__total {
      @include rfs(18px);
    }
  }

  &__single {
    padding: 5px 8px;
    display: flex;
    align-items: center;

    span,
    strong {
      padding: 0 5px;

      &.label-detailed__status {
        padding-left: 0;
      }
    }
  }

  &__type {
    font-size: 14px;
    font-weight: 400;
    display: inline-flex;
    align-items: center;
    color: var(--color-gray);

    &::before {
      content: '';
      width: 7px;
      height: 7px;
      border-radius: 50%;
      background: #eee;
      display: inline-block;
      margin-right: 5px;
    }

    &--primary {
      &::before {
        background: var(--color-primary);
      }
    }

    &--info {
      &::before {
        background: var(--color-info);
      }
    }
  }

  &__total {
    font-size: 22px;
    font-weight: 600;
    color: var(--color-dark);
  }

  &__status {
    display: inline-flex;
    align-items: center;
    line-height: normal;

    i {
      font-size: 11px;
    }

    img,
    svg {
      width: 11px;
      height: 11px;
      margin-left: 5px;

      +strong {
        margin-left: 5px;
      }
    }

    strong {
      padding: 0;
      font-size: 14px;
      font-weight: 500;
    }
  }
}

