/* Performance overview */
.performance-o {
  height: 100%;

  .card-body {
    display: flex;
    align-items: center;

    @include xl {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }

  .performance-chart-labels {
    @include xl {
      margin: 0 !important;

      ul {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-top: 15px;

        li {
          margin: 0 !important;
        }
      }
    }

    @include xs {
      ul {
        flex-wrap: wrap;
        justify-content: center
      }
    }
  }
}

.performance-chart-wrap {
  display: flex;
  align-items: center;
}

.performance-chart {
  width: 285px;
  height: 285px;
  margin: -25px;
}

.performance-chart-labels {
  margin-left: 38px;

  ul {
    li {
      font-size: 15px;
      font-weight: 400;
      display: flex;
      align-items: center;
      color: var(--color-gray);

      &:not(:last-child) {
        margin-bottom: 12px;
      }

      &::before {
        content: '';
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background: #ccc;
        margin-right: 7px;
      }

      &.label-target {
        color: var(--color-gray);

        &::before {
          background: var(--color-primary);
        }
      }

      &.label-completed {
        &::before {
          background: var(--color-info);
        }
      }

      &.label-in-progress {
        &::before {
          background: var(--color-warning);
        }
      }
    }
  }
}
.storage .apexcharts-datalabel-value{
	font-family: 'Jost', sans-serif !important;
}